<template>
  <div class='echart-container'>
    <v-chart :options="option"></v-chart>
  </div>
</template>

<script>
import 'echarts/lib/chart/pie'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/legend'

export default {
  data(){
    return {
      option : {
        tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
            orient: 'vertical',
            left: 10,
            data: ['', '', '', '', '']
        },
        series: [
            {
                name: '',
                type: 'pie',
                radius: ['50%', '70%'],
                avoidLabelOverlap: false,
                label: {
                    show: false,
                    position: 'center'
                },
                emphasis: {
                    label: {
                        show: true,
                        fontSize: '30',
                        fontWeight: 'bold'
                    }
                },
                labelLine: {
                    show: false
                },
                data: [
                    {value: 335, name: ''},
                    {value: 310, name: ''},
                    {value: 234, name: ''},
                    {value: 135, name: ''},
                    {value: 1548, name: ''}
                ]
            }
        ]
    }

    }
  }
}
</script>

<style>
  .echart-title{
    margin: 10px;
    text-align: center;
    font-size: 26px;
  }

  /**
  * The default size is 600px400px, for responsive charts
  * you may need to set percentage values as follows (also
  * don't forget to provide a size for the container).
  */
  .echarts {
    width: 375px;
    height: 300px;
    margin: 0 auto;
  }
</style>